<template>
  <div id="app">
    <router-view/>
    <BackTop :duration="2000"></BackTop>
  </div>
</template>

<script>
  export default {
    name: 'App',
    created() {
      try {
        document.body.removeChild(document.getElementById('appLoading'));
        setTimeout(function () {
          document.getElementById('app').style.display = 'block';
        }, 500)
      } catch (e) {
        console.log(e);
      }
    }
  }
</script>

<style>
  body {
    margin: 0;
    padding: 0;
    overflow-y: scroll;
  }
</style>



