<template>
  <div id="content">
    <div class="box" style="background-color: #F5F5F5">
      <transition name="fade">
        <h3 v-if="isShows[0].isShow">You're unique, nothing can replace you.</h3>
      </transition>
    </div>

    <div class="box" style="background-color: white">
      <transition name="fade">
        <h3 v-if="isShows[1].isShow">You're unique, nothing can replace you.</h3>
      </transition>
    </div>

    <div class="box" style="background-color: #F5F5F5">
      <transition name="fade">
        <h3 v-if="isShows[2].isShow">You're unique, nothing can replace you.</h3>
      </transition>
    </div>

    <div class="box" style="background-color: white">
      <transition name="fade">
        <h3 v-if="isShows[3].isShow">You're unique, nothing can replace you.</h3>
      </transition>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Content",
    data() {
      return {
        isShows: [
          {isShow: false},
          {isShow: false},
          {isShow: false},
          {isShow: false}
        ],
        progress: 0
      }
    },
    mounted() {
      //  useCapture——true: 外部元素的事件先触发
      window.addEventListener('scroll', this.showBox, true);
    },
    destroyed() {
      window.removeEventListener('scroll', this.showBox)
    },
    methods: {
      //  动态显示boxes
      showBox: function () {
        const boxes = document.querySelectorAll('.box');
        let length = boxes.length;

        for (let index = 0; index < length; index++) {
          let element = boxes[index];
          const viewPortHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
          const top = element.getBoundingClientRect() && element.getBoundingClientRect().top;

          if (document.documentElement.scrollTop <= 100) {
            this.progress = 0;
            this.$Loading.update(this.progress);
            continue;
          }

          if (top <= viewPortHeight - 100) {
            if (!this.isShows[index].isShow) {
              this.isShows[index].isShow = true;
            }
            let num = (index + 1) * (1 / length) * 100;
            if (this.progress != num) {
              this.progress = num;
            }
            this.$Loading.update(this.progress);
          }
        }
      },
    },
    watch: {},
  }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    text-align: center;
  }

  .box {
    height: 500px;
    width: 100%;
  }

  h3 {
    color: black;
    font-family: "Nunito", Arial, sans-serif;
    font-size: 40px;
    font-weight: 300;
  }

  .fade-enter, .fade-leave-to {
    opacity: 0;
    transform: translateY(75px);
  }

  .fade-enter-to, .fade-leave {
    opacity: 1;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 0.8s;
  }
</style>



